    <v-app-bar color="#2195F2" dark app>
      <v-app-bar-nav-icon @click="back">
        <v-icon>mdi-arrow-left</v-icon>
      </v-app-bar-nav-icon>

      <v-toolbar-title>{{vdata.title}} • {{selecedTabTitle}}</v-toolbar-title>

      <div class="flex-grow-1"></div>

      <v-btn to="/" icon>
        <v-icon>mdi-home</v-icon>
      </v-btn>

      <v-btn icon>
        <v-icon>mdi-magnify</v-icon>
      </v-btn>

      <v-menu left bottom>
        <template v-slot:activator="{ on }">
          <v-btn icon v-on="on">
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>

        <AppbarMenus v-on:menuDialog-show="listenDialog" />
      </v-menu>
      <template v-slot:extension>
        <v-tabs align-with-title background-color="transparent" center-active>
          <v-tab
            @click="tabClick(item)"
            v-for="(item, index) in vdata.language"
            :key="index"
          >{{item}}</v-tab>
        </v-tabs>
      </template>
    </v-app-bar>
    <v-dialog v-model="dialog" max-width="290">
      <v-card>
        <v-card-title class="headline">{{dialogData.title}}</v-card-title>
        <v-card-text>{{dialogData.content}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="dialog = false">确认</v-btn>
