<script>
import AppbarMenus from "@/components/appbar-menus.vue";
export default {
  components: {
    AppbarMenus
  },
  props: {
    vdata: Object
  },
  data() {
    return {
      dialog:false,
      selecedTabTitle: "ALL",
      drawer: false,
      dialogData: {
        title: "",
        content: ""
      }
    };
  },
  mounted() {
    //this.selecedTabTitle=this.vdata.language[0];
  },
  methods: {
    tabClick(item) {
      console.info(item);
      this.selecedTabTitle = item;
      this.$emit("tab-click", item);
    },
    back() {
      this.$router.back(-1);
    },
    listenDialog(data) {
      this.dialog = true;
      this.dialogData = data;
    }
  }
};
