<template>
  <div>
    <v-list>
      <v-list-item v-for="n in barsMenu" :key="n" @click="barsMenuClick(n)">
        <v-list-item-title>{{ n }}</v-list-item-title>
      </v-list-item>
    </v-list>

  
  </div>
</template>
<script>
export default {
  data() {
    return {
     
      barsMenu: ["关于"]
    };
  },
  methods: {
    barsMenuClick(n) {
      switch (n) {
        case "关于":
            let dialogData={
                title:"VaemcDesign",
                content:"网站还在完善中，有任何问题请联系QQ：1224657851"
            }
           this.$emit("menuDialog-show", dialogData);
          break;
      }
    }
  }
};
</script>